.advantages {
    @include adaptiv-value('padding-top', 88, 30, 1);
    @include adaptiv-value('padding-bottom', 88, 30, 1);
    &__container {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &__item {
        display: inline-grid;
        padding: 15px 0;
        grid-template: auto auto / 85px 1fr;
        @media (max-width: $md2) {
            min-width: 280px;
        }
        @media (max-width: $md3) {
            grid-template: auto auto / auto 1fr;
        }
    }
    &__icon {
        text-align: center;
        align-self: center;
        grid-row: span 2;
        @media (max-width: $md3) {
            padding: 0px 15px 0px 0px;
        }
    }
    &__title {
        font-weight: 600;
        font-size: 18px;
        line-height: 150%;
        margin: 0px 0px calc(2px/18px * 1em) 0px;
    }
    &__text {
        line-height: 150%;
        color: $grayColor;
    }
}